import React, { Component } from 'react';
import FontAwesomeIcon from 'react-fontawesome';
import './index.css';

const styles = {
  item: {
    marginBottom: 30,
  },
};

class Service extends Component {
  renderServices() {
    return (
      <div className="row">
        {this.renderMobileApp()}
        {this.renderWebApp()}
        {this.renderBackend()}
      </div>
    );
  }

  renderBackend() {
    return (
      <div className="col-md-4">
        <div style={{display: 'flex', alignItems: 'center',}}>
          <div style={{
            width: 50,
              height: 50,
              borderRadius: 25,
              backgroundColor: '#DAFBED',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
          }}>
            <FontAwesomeIcon
              name="server"
              style={{color: '#20E1C2'}}
            />
          </div>
          <span style={{marginLeft: 10, textTransform: 'uppercase'}}>
            Backend integration
          </span>
        </div>
        <div style={{fontSize: 15, padding: 10}}>
          Maintain high availability and scalable backend solution for your applications. Build API and architect necessary infrastructure.
        </div>
      </div>
    );
  }

  renderWebApp() {
    return (
      <div className="col-md-4" style={{marginBottom: '2rem'}}>
        <div style={{display: 'flex', alignItems: 'center',}}>
          <div style={{
            width: 50,
              height: 50,
              borderRadius: 25,
              backgroundColor: '#FFD4D4',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
          }}>
            <FontAwesomeIcon
              name="desktop"
              style={{color: '#FF5657'}}
            />
          </div>
          <span style={{marginLeft: 10, textTransform: 'uppercase'}}>
            Build web app
          </span>
        </div>
        <div style={{fontSize: 15, padding: 10}}>
          Develop web application to handle access for different user role. E.g: Merchant portal/Admin dashboard.
        </div>
      </div>
    );
  }

  renderMobileApp() {
    return (
      <div className="col-md-4" style={{marginBottom: '2rem'}}>
        <div style={{display: 'flex', alignItems: 'center',}}>
          <div style={{
            width: 50,
              height: 50,
              borderRadius: 25,
              backgroundColor: '#D9EAFF',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
          }}>
            <FontAwesomeIcon
              name="mobile"
              size="2x"
              style={{color: '#089FFF'}}
            />
          </div>
          <span style={{marginLeft: 10, textTransform: 'uppercase'}}>
            Develop mobile app
          </span>
        </div>
        <div style={{fontSize: 15, padding: 10}}>
          Build your next mobile applications with React Native, a cross platform framework that gives you Android and iOS apps with single code base.
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="section">
        <div className="container">
          <div className="main-title">
            <h4 className="">
              Services
            </h4>
            <div className="main-title-underline" />
          </div>

          {this.renderServices()}
        </div>
      </div>
    );
  }
}

export default Service;



