import React, { Component } from 'react';
import './index.css';
import ShowcaseItem from '../ShowcaseItem';
import _ from 'lodash';
import GA from '../../lib/utils/GA';
import { apps } from '../../lib/constants/projects';

class Showcase extends Component {
  state = {
  };

  renderApp(app) {
    return (
      <ShowcaseItem
        key={app.id}
        app={app}
        trackEvent={GA.trackLinkEvent}
      />
    );
  }

  renderApps() {
    return (
      <div className="section">
        <div className="main-title">
          <h4 className="">
            Projects
          </h4>
          <div className="main-title-underline" />
        </div>

        <div className="row">
          {
            apps.map(app => (
              this.renderApp(app)
            ))
          }
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="section-colored-bg">
        <div className="container">
          {this.renderApps()}
        </div>
      </div>
    );
  }
}

export default Showcase;



