import React, { Component } from 'react';
import './index.css';
import {
  Card,
  CardActions,
  CardHeader,
  // CardMedia,
  CardTitle,
  CardText
} from 'material-ui/Card';
import FontAwesomeIcon from 'react-fontawesome';

class ShowcaseItem extends Component {
  renderAppLink(type = 'ios') {
    const { app, trackEvent } = this.props;
    let link = app.iosLink;

    if (type === 'android') {
      link = app.androidLink;
    }
    if (!link) return null;

    return (
      <a href={link} target="_blank" onClick={() => trackEvent(link)}>
        <FontAwesomeIcon
          name={type === 'ios' ? 'apple' : 'android'}
          size="2x"
        />
      </a>
    );
  }

  renderAppWebsite() {
    const { app, trackEvent } = this.props;

    if (!app.website) return null;

    return (
      <a
        href={app.website}
        target="_blank"
        rel="noopener noreferrer"
        onClick={() => trackEvent(app.website)}
      >
        <CardTitle title='' subtitle={app.website} style={{}}/>
      </a>
    );
  }

  render() {
    const { app, trackEvent } = this.props;

    return (
      <div className="col-lg-4 col-card">
        <Card style={{ fontFamily: 'Raleway'}}>
          <CardHeader
            title={app.title}
            subtitle={app.subtitle}
            avatar={(
              <img className="app-icon-shadow" src={app.icon} alt="" />
            )}
            style={{display: 'flex'}}
            textStyle={{paddingRight: 0}}
          />
          {this.renderAppWebsite()}
          <CardText>
            {app.description}
          </CardText>
          <CardActions>
            {this.renderAppLink()}
            {this.renderAppLink('android')}
          </CardActions>
        </Card>
      </div>
    );
  }
}

export default ShowcaseItem;



