import React, { Component } from 'react';
import {Card} from 'material-ui/Card';
import './index.css';
import RaisedButton from 'material-ui/RaisedButton';
import GA from '../../lib/utils/GA';
import me from '../../lib/constants/me';

const images = {
  avatar: require('../../assets/avatar/cl-min.jpg'),
  bg: require('../../assets/bg.jpg'),
};

const styles = {
  avatar: {
    width: 120,
    height: 120,
    borderRadius: 60,
    maxWidth: 120,
  },
  content: {
    paddingBottom: 12,
    paddingLeft: 12,
    height: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    color: 'white',
  },
};

class About extends Component {
  state = {
  };

  render() {
    return (
      <div className="bg-section" style={styles.content}>
        <div className="bg-main" />
        <div className="container">
          <div className="intro">
            <div>
              Build your next mobile app with React Native
            </div>
              <a
                className="contact-btn"
                href={me.mailto}
                onClick={() => {GA.trackLinkEvent('contactEmail')}}
              >
                Contact me
              </a>
          </div>
        </div>
      </div>
    );
  }
}

export default About;



