import React, { Component } from 'react';
import './index.css';
import {Card, CardHeader, CardText} from 'material-ui/Card';

const images = {
  edvin: require('../../assets/avatar/edvin.jpg'),
  rahim: require('../../assets/avatar/rahim.jpeg'),
  joerg: require('../../assets/avatar/joerg.jpeg'),
};

const styles = {
  item: {
    marginBottom: 30,
  },
};

const recommendations = [
  {
    id: 'edvin',
    name: 'Edvin Fladen',
    title: 'Entrepreneur',
    text: "A great developer and a nice person. This is my impression from working with Chin Loong. I had the pleasure of working with Chin Loong on a MVP project based on React Native. His skills, speed and professionalism where beyond my expectation. While juggling two other projects, he was still able to come up with solutions and he truly cared for the success of the project. He is easy to collaborate with, and I am confident Chin Loong will do great things in the future.",
    avatar: images.edvin,
  },
  {
    id: 'rahim',
    name: 'Rahim Rahman',
    title: 'Senior Software Developer at Aptos Retail',
    text: "I have worked with Chin Loong on a few React-Native projects and no matter how complex the issues assigned to him, he has always been able to come up with solutions that I have confidence in. He has been handling his tasks really well, willing to accept feedback and always eager to learn new things. Hands down, he is one of the best software engineers I've had the privilege of working with in my career.",
    avatar: images.rahim,
  },
  {
    id: 'joerg',
    name: 'Jörg Ossenkopp',
    title: 'Head of Frontend bei Parkling',
    text: "Chin Loong is a very nice and bright guy, great to work with. He is resourceful and knowledgeable, a quick learner. He helped us to solve some complex React Native problems in our app and built our website with Ruby on Rails. I would always work again with him, with pleasure, and I can recommend him wholeheartedly.",
    avatar: images.joerg,
  },
];

class Recommendation extends Component {
  renderRecommendation(recommendation, index) {
    return (
      <div key={`${recommendation.id}-${index}`} className="col-md-4" style={styles.item}>
        <Card key={recommendation.id} style={{ fontFamily: 'Raleway'}}>
          <CardHeader
            title={recommendation.name}
            subtitle={recommendation.title}
            // avatar={recommendation.avatar}
            avatar={(
              <img className="avatar-shadow" src={recommendation.avatar} alt="" />
            )}
            style={{display: 'flex'}}
            textStyle={{paddingRight: 0}}
          />
          <CardText>
            {recommendation.text}
          </CardText>
        </Card>
      </div>
    );
  }

  render() {
    return (
      <div className="section">
        <div className="container">
          <div className="main-title">
            <h4 className="">
              Recommendations
            </h4>
            <div className="main-title-underline" />
          </div>

          <div className="row">
            {recommendations.map((recommendation, index) => (
              this.renderRecommendation(recommendation, index)
            ))}
          </div>
        </div>
      </div>
    );
  }
}

export default Recommendation;



